.drill .container>div{opacity:1;-webkit-transition:opacity .3s;-o-transition:opacity .3s;transition:opacity .3s}.drill .container>div.card-exit{opacity:0}.drill h1,.drill h3{border:.2em solid white;border-radius:1em;height:1em;left:50%;margin-left:-.75em;margin-top:-.75em;padding:.1em;position:absolute;text-align:center;top:40%;width:1em}.drill h1{font-size:20vh}.drill h3{background-color:#e5e9f1;border-color:white;border-radius:1.5em;font-size:12vh;line-height:1.2em;text-align:center;-webkit-transition:all 500ms;-o-transition:all 500ms;transition:all 500ms;vertical-align:bottom}.drill h3.incorrect{color:white;background-color:white;cursor:default}.drill h3.correct{background-color:white;border-color:#01b601;cursor:default}.drill .answer-0{margin-left:-2.75em}.drill .answer-1{margin-left:1.35em}.drill .answer-2{margin-top:-2.8em}.drill .answer-3{margin-top:1.35em}.drill .answer-4{margin-left:-2.75em;margin-top:-1.4em;top:96%;width:5em}@media (max-width: 768px){.drill h1{font-size:10vh}.drill h3{font-size:6vh}}



